const app = getApp()
export default {
	//免费注册
	data() {
		return {
			countdowntext:"验证码",
			wait:60,
			disabled:true,
			username:"",
			phone:"",
			times:null
		}
	},
	methods: {
		//验证码
		countdown(){
			this.regphone()
			//在app.vue里面的globalData对象中封装了方法 用来请求信息 用户注册的时候传用户名
			let json = {
				mobile:this.phone,
				type:2,
				username:this.username
			}
			// console.log(json)
			//在app.vue中封装了函数 用来请求短信验证码
			app.globalData.VerificationCode(json)
			this.time()
		},
		//验证手机号
		validationphone(){
			this.regphone()
		},
		//封装个匹配手机号的方法
		regphone(){
			let userphone = /^1([38][0-9]|4[579]|5[0-3,5-9]|6[6]|7[0135678]|9[89])\d{8}$/
			//用户和手机不能为空
			if(this.username && this.phone.match(userphone)){
				this.disabled = false
				clearInterval(this.times)
			}else{
				this.disabled = true
				clearInterval(this.times)
				uni.showToast({
					title:"请输入正确的手机号或者用户名不能为空",
					icon:"none"
				})
			}
		},
		//验证码的秒数
		time(){
			this.disabled = true
			//这块点击反复执行定时器
			// clearInterval(times)
			let {countdowntext,wait} = this.$data
			// console.log(countdowntext,wait)
				this.times = setInterval(()=>{
					wait--
					// console.log(wait)
					this.countdowntext = wait
					if(wait==0){
						this.disabled = false
						countdowntext = "重新获取验证码"
						clearInterval(this.times)
						this.countdowntext = countdowntext
						this.wait = 60
					}
					
				},1000)
		},
		//提交
		smslogin(e){
			// console.log(e)
			//获取里面的每一个值
			let {username,password,phone,phonecode} = e.detail.value
			//写两个正则
			//来匹配账号
			//账号必须为5到100位
			let regname = /^[\W|\w]{5,100}$/;
			//密码为6-16位 单词，数字加_
			let userpassword = /^\w{6,16}$/;
			let userphone = /^1([38][0-9]|4[579]|5[0-3,5-9]|6[6]|7[0135678]|9[89])\d{8}$/
			if(username.match(regname) && password.match(userpassword) && phone.match(userphone) && phonecode!==""){
				console.log("验证通过")
				//如果都通过了 发起请求 就可以跳转到登录页面
				//这里是要传给后台的信息
				//这是app端的数据
				let registeredjson = {
					username:username,
					password:password,
					mobile_phone:phone,
					code:phonecode
				}
				//将用户微信与微信绑定的参数
				let bingjson = {
					login_type:"weixin",
					bind_type:"account",
					username:username,
					password:password
				}
				//这块来获取缓存中的微信的code码  拿code码去后端换取openid

					//取缓存中值
					uni.getStorage({
						key:"wxcodekey",
						success(res){
							console.log(res.data)
							//成功了 就把openid的码 给对象新增加了属性
							registeredjson.openid = res.data
							bingjson.opened = res.data
							console.log(bingjson)
							uni.request({
								url:"http://hbk.huiboke.com/api/login_and_register/userRegister",
								method:"POST",
								data:registeredjson,
								success(ress){//请求成功的时候
									console.log(ress)
									if(ress.data.code==0){
											uni.request({
												url:"http://hbk.huiboke.com/api/login_and_register/userLogin",
												method:"POST",
												data:bingjson,
												success(resquicklogin) {
													console.log(resquicklogin)//当用户注册和绑定成功了 把tokey值存一下 把状态存一下
													if(resquicklogin.data.code==0){
														//设置用户绑定的tokey
														uni.setStorage({
															key:"bindtokey",
															data:resquicklogin.data.data.token
														})
														//这里设置用户的登录状态码 为1
														uni.setStorage({
															key:"loginstate",
															data:1
														})
														uni.setStorage({
															key:"userbindstate",
															data:1,
															success() {
																uni.switchTab({
																	url:"/pages/index/index"
																})
															}
														})
													}else{
														uni.setStorage({
															key:"loginstate",
															data:0
														})
													}
												}
											})
									}else{
										uni.showToast({
											title:"该用户已经注册过了",
											icon:"none"
										})
									}
								},
								fail(){//请求失败的时候
									uni.showToast({
										title:"注册失败",
										icon:"none"
									})
								}
							})
						}
					})

				//这里进行请求




























			}else{
				uni.showToast({
					title:"您填写的信息不正确",
					icon:"none"
				})
			}
		}
	}
}
